// src/app/carrinho/page.js
'use client';

import { useState } from 'react';
import { useCart } from '../../context/CartContext';
import { QRCodeCanvas } from 'qrcode.react';

export default function CarrinhoPage() {
  const { cartItems, clearCart } = useCart();
  const [showSummary, setShowSummary] = useState(false);

  // Agrupa os produtos repetidos
  const groupedItems = cartItems.reduce((acc, item) => {
    const found = acc.find(i => i.id === item.id);
    if (found) {
      found.quantity++;
    } else {
      acc.push({ ...item, quantity: 1 });
    }
    return acc;
  }, []);

  const totalPrice = groupedItems.reduce(
    (acc, item) => acc + item.price * item.quantity,
    0
  );

  const totalQuantity = groupedItems.reduce(
    (acc, item) => acc + item.quantity,
    0
  );

  // Formatar mensagem para WhatsApp
  const message = `Olá, gostaria de finalizar meu pedido:\n\n` +
    groupedItems.map((item, idx) => `${idx + 1}. ${item.name} x${item.quantity} - R$ ${(item.price * item.quantity).toFixed(2)}`).join('\n') +
    `\n\nTotal: R$ ${totalPrice.toFixed(2)}`;

  const whatsappLink = `https://wa.me/5511999999999?text=${encodeURIComponent(message)}`;

  function handleEnviarWhatsapp() {
    window.open(whatsappLink, '_blank');
    clearCart();
  }

  return (
    <div className="p-4 max-w-xl mx-auto">
      <h1 className="text-2xl font-bold mb-6">Carrinho</h1>

      {groupedItems.length === 0 ? (
        <p>Seu carrinho está vazio.</p>
      ) : (
        <>
          <ul className="mb-4 max-h-48 overflow-y-auto">
            {groupedItems.map((item, idx) => (
              <li
                key={item.id}
                className="flex justify-between"
              >
                <span>{idx + 1}. {item.name} x{item.quantity}</span>
                <span>R$ {(item.price * item.quantity).toFixed(2)}</span>
              </li>
            ))}
          </ul>

          <p className="font-semibold text-lg text-right mb-4">
            Total ({totalQuantity} itens): R$ {totalPrice.toFixed(2)}
          </p>

          <button
            onClick={() => setShowSummary(true)}
            className="w-full bg-red-600 hover:bg-red-700 text-white py-2 rounded-lg mb-4 transition"
          >
            Finalizar Pedido
          </button>

          {showSummary && (
            <div className="bg-gray-100 p-4 rounded-lg shadow-md">
              <h2 className="text-xl font-semibold mb-4">Resumo do Pedido</h2>
              <ul className="mb-4 max-h-48 overflow-y-auto">
                {groupedItems.map((item, idx) => (
                  <li
                    key={item.id}
                    className="flex justify-between"
                  >
                    <span>{idx + 1}. {item.name} x{item.quantity}</span>
                    <span>R$ {(item.price * item.quantity).toFixed(2)}</span>
                  </li>
                ))}
              </ul>

              <p className="font-semibold text-lg text-right mb-4">
                Total: R$ {totalPrice.toFixed(2)}
              </p>

              <div className="flex justify-center mb-4">
                <QRCodeCanvas value={whatsappLink} size={180} />
              </div>

              <button
                onClick={handleEnviarWhatsapp}
                className="w-full bg-green-600 hover:bg-green-700 text-white py-2 rounded-lg transition"
              >
                Enviar WhatsApp
              </button>
            </div>
          )}
        </>
      )}
    </div>
  );
}

